.product {
  width: 300px;
  height: 400px;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 10px;
  position: relative;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.product:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.productPictureArea {
  margin-top: 40px;
  height: 250px;
  width: 230px;
  overflow: hidden;
}

.productPicture {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.productTextArea {
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.productName {
  margin: 2px 5px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
}

.productPrice {
  margin: 2px 5px;
  opacity: 0.7;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
}

.soldArea {
  position: absolute;
  top: -200px;
  left: -15px;
}

.soldArea--hidden {
  display: none;
}

.sold {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  font-size: 55px;
  border-radius: 5px;
  background-color: rgba(236, 228, 228, 0.5);
  color: black;
}
.productListArea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
}

.productsList {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.pages {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.pagesList {
  display: flex;
  list-style: none;
}

.pagesItem {
  margin: 0 5px;
  width: 24px;
  height: 17px;
  opacity: 0.4;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 24px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 1.2px;
}

.pagesItem--active {
  opacity: 1;
}

.pageArrowRight {
  line-height: 28px;
  opacity: 0.4;
  margin: 0 5px;
}
.sideBar {
  width: 40%;
}

.sideBarList {
  padding-top: 5px;
  height: 150px;
  width: 200px;
  border-bottom: 1px solid rgba(0, 0, 0, .5);
}

.sideBarItem {
  list-style: none;
  margin: 10px 0;
  height: 20px;
  opacity: 0.7;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1.15px;
  cursor: pointer;
}

.sliderArea {
  margin-top: 10px;
}

.sliderTitle {
  margin-top: 10px;
}
.carouselImage {
  height: 235px;
  object-fit: contain;
}

.carouselIconImg {
  background-color: darkgrey
}

.carousel-indicators li {
  border: solid 1px black;
  border-radius: 5px;
}

.carouselProduct {
  height: 30px;
  border-radius: 5px;
  background-color: rgba(236, 228, 228, 0.5);
  line-height: 30px;
  color: black;
  font-weight: bold;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

#root {
  width: 1000px;
  margin: 0 auto;
}

.pageContainer {
  display: flex;
  width: 100%;
  min-height: 850px;
  display: flex;
  justify-content: center;
}

.link {
  text-decoration: none;
  color: black;
}

.link:hover {
  text-decoration: none;
}

.subpageArea {
  display: flex;
  flex-direction: column;
  width: 100%
}

.subpageTitle {
  margin-bottom: 20px;
}

.notFoundTextArea {
  position: absolute;
  cursor: pointer;
  border: solid 2px white;
  border-radius: 10px;
  top: 550px;
  background-color: white;
  height: 30px;
}
.menu {
  margin: 10px 0 30px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30px;
}

.logoArea {
  width: 80px;
  height: 100%;
}

.logoPicture {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.menuArea {
  display: flex;
  justify-content: flex-end;
  height: 100%;
  flex: 2 1;
}

.menuList {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}

.menuItem {
  margin: 0 10px;
  height: 12px;
  opacity: 0.5;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 6.4px;
  cursor: pointer;
}

.menuItem--Active {
  opacity: inherit;
}

.cartIconArea {
  position: relative;
}

.cartIcon {
  line-height: 25px;
  height: 40px;
}

.productsInCart {
  letter-spacing: 0;
  color: #000000;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: #d3cdc9;
  position: absolute;
  bottom: -30px;
  left: 10px;
  opacity: 0.7;
  text-align: center;
  line-height: 35px;
}
.footer {
  margin: 10px 0 10px 0;
}

.allRightsArea {
  line-height: 40px;
  opacity: 0.5;
  font-size: 10px;
  width: 150px;
  display: inline-block;
}

.footerArea {
  float: right;
}

.footerList {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}

.footerItem {
  margin: 0 10px;
  height: 12px;
  opacity: 0.5;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 12px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 6.4px;
  cursor: pointer;
}

.footerItem--Active {
  opacity: inherit;
}
.productDetailComponent {
  height: 600px;
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
}

.productDetailContainer {
  height: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.productDetailImageArea {
  height: 450px;
  width: 450px;
}

.productDetailImage {
  object-fit: cover;
  max-height: 100%;
  max-width: 100%;
}

.productDetailDescriptions {
  padding-left: 15px;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.productDetailTitle {
  width: 101px;
  height: 18px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 24px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 1.2px;
  margin-bottom: 18px;
}

.productDetailPrice {
  width: 53px;
  height: 15px;
  opacity: 0.7;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
  margin-bottom: 18px;
}

.productDetailText {
  width: 509px;
  height: 250px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
}

.buttonCart {
  width: 258px;
  height: 45px;
  background-color: #000000;
  color: white;
  cursor: pointer;
}

.buttonCart:hover {
  background-color: darkgrey;
  color: #000000;
  -webkit-transition: 1s all;
  transition: 1s all;
}

.buttonCart--empty {
  width: 400px;
  background-color: rgb(105, 107, 103);
  color: black;
  cursor: none;
}
.cartComponent {
  height: 300px;
  width: 1000px;
  margin: 10px 0;
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
}

.cartContainer {
  height: 250px;
  width: 100%;
  display: flex;
  margin: 10px 0;
  justify-content: center;
  align-items: center;
}

.cartImageArea {
  height: 250px;
  width: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cartProductImage {
  object-fit: cover;
  max-height: 100%;
  max-width: 100%;
}

.cartProductDescriptions {
  width: 65%;
  padding-left: 15px;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
}

.cartProductTitle {
  height: 22px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 24px;
  font-weight: bold;
  line-height: 27px;
  letter-spacing: 1.2px;
  margin-bottom: 18px;
}

.cartProductText {
  text-overflow: ellipsis;
  overflow: hidden;
  height: 152px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
}


.cartProductDescriptionsContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
}

.cartProductBuyingArea {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  position: relative;
}

.cartProductBuyingContainer {
  display: flex;
  justify-content: center;
}

.buttonBuy {
  margin-top: 30px;
  width: 258px;
  height: 45px;
  background-color: #000000;
  color: white;
  cursor: pointer;
}

.cartProduct {
  padding: 15px;
  height: 15px;
  color: #000000;
  font-family: "Myriad Pro";
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0.9px;
  text-align: center;
}

.cartProduct--grey {
  opacity: 0.7;
}

.cartProductDisabled {
  opacity: 0.7;
  color: grey;
}

.cartProductClick {
  cursor: pointer;
}

.cartDelProductButton {
  color: #ff4444;
  font-family: "Myriad Pro";
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
  position: absolute;
  right: 15px;
  bottom: 30px;
  cursor: pointer;
}

.cartFinishArea {
  height: 200px;
  width: 100%;
  color: #000000;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  padding-right: 20px;
}

.hide {
  display: none;
}

.emptyCart {
  margin: 20px;
  display: flex;
  justify-content: flex-end;
}
